import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';

const ParkingDetailPage = () => {
    const { id } = useParams(); //  ID  URL
    const [parkingSpace, setParkingSpace] = useState(null);

    useEffect(() => {
        //      
        axios.get(`/api/parking/parking-spaces/${id}`)
            .then((response) => {
                setParkingSpace(response.data);
            })
            .catch((error) => {
                console.error('   :', error);
            });
    }, [id]);

    if (!parkingSpace) {
        return <div>Loading...</div>;
    }

    return (
        <div>
            <h1>Parking Space #{parkingSpace.number}</h1>
            <p>Status: {parkingSpace.isOccupied ? 'Occupied' : 'Free'}</p>
        </div>
    );
};

export default ParkingDetailPage;